import { FC } from 'react';

// Components
import { Panel } from '../PanelLayout';

// Types
import { PanelTemplateProps } from 'types';

const HeaderPanel: FC<PanelTemplateProps> = ({
  children,
  name,
  ...otherProps
}) => {
  return (
    <Panel name={name || 'header'} {...otherProps}>
      {children}
    </Panel>
  );
};

export default HeaderPanel;
